<style>
.echarts {
  width: 40%;
  height: 500px;
  padding: 10px;
  border: 1px solid #ddd;
  float: left;
  margin-left: 80px;
}
.chart-boday {
  width: 100%;
  height: 100%;
  float: left;
}
</style>

<template>
  <div class="chart-boday">
      <div id="tech" class="echarts"></div>
      <div id="people" class="echarts"></div>
  </div>
</template>

<script>
// import ECharts from "vue-echarts/components/ECharts";

// import theme from '../theme.json'
// ECharts.registerTheme('ovilia-green', theme); //引入主题
import echarts from 'echarts'
export default {
  data() {
    return {
        jishu: {},
        count: 1,
    }
  },
  created() {
  },
  methods: {
    getTechData () {
         // 基于准备好的dom，初始化echarts实例
        var myChart = this.$echarts.init(document.getElementById('tech'))
        // 绘制图表
        var option = {
                        "title": {
                            "text": "多屏人员统计",
                            "subtext": "动态数据",
                            "x": "center"
                        },
                        "tooltip": {
                            "trigger": "item",
                            "formatter": "{a} <br/>{b} : {c} ({d}%)"
                        },
                        "legend": {
                            "show": true,
                            "orient": "vertical",
                            "left": "left",
                            "data": ["关键角色", "骨干", "普通", "新人"]
                        },
                        "series": [
                            {
                            "name": "人员统计",
                            "type": "pie",
                            "radius": "55%",
                            "center": ["50%", "60%"],
                            "data": [
                                { "value": 8, "name": "关键角色" },
                                { "value": 16, "name": "骨干" },
                                { "value": 7, "name": "普通" },
                                { "value": 5, "name": "新人" }
                            ],
                            "itemStyle": {
                                "emphasis": {
                                "shadowBlur": 10,
                                "shadowOffsetX": 0,
                                "shadowColor": "rgba(0, 0, 0, 0.5)"
                                }
                            }
                            }
                        ]
                    }
        myChart.setOption(option);
    },
    getPeopleData () {
         var myChart = this.$echarts.init(document.getElementById('people'))
         var option = {
                            "title": {
                                "text": "团队技能雷达图"
                            },
                            "tooltip": {},
                            "legend": {
                                "data": ["测试", "开发"]
                            },
                            "radar": {
                                // shape: 'circle',
                                "name": {
                                "textStyle": {
                                    "color": "#fff",
                                    "backgroundColor": "#999",
                                    "borderRadius": 3,
                                    "padding": [3, 5]
                                }
                                },
                                "indicator": [
                                                { "name": "vue框架", "max": 10 },
                                                { "name": "angular框架", "max": 10 },
                                                { "name": "react框架", "max": 10 },
                                                { "name": "黑盒测试", "max": 10 },
                                                { "name": "性能测试", "max": 10 },
                                                { "name": "APP测试", "max": 10 },
                                                { "name": "IOS开发", "max": 10 },
                                                { "name": "安卓开发", "max": 10 }
                                            ]
                            },
                            "series": [
                                {
                                "name": "111",
                                "type": "radar",
                                // areaStyle: {normal: {}},
                                "data": [
                                    {
                                    "value": [5, 6, 7, 8, 6, 4, 6, 5, 8],
                                    "name": "测试"
                                    },
                                    {
                                    "value": [5, 6, 7, 8, 6, 4, 6, 5, 8],
                                    "name": "开发"
                                    }
                                ]
                                }
                            ]
                        }

         myChart.setOption(option);
    }
  },
  mounted() {
      this.getTechData();
      this.getPeopleData();
  }
};
</script>


